// color Pattle
@include initColor($magenta, true, 2);
@include initColor($blue, true, 2);
@include initColor($mint, true, 2);
@include initColor($yellow, true, 2);
@include initColor($red, true, 2);
@include initColor($gray, true, 2);
@include initColor($basic,false);
@include initColor($span,false);
@include initColor($point,false);
@include initColor($link,false);
@include initColor($bt4,false);

.color-palette {
	article {
		padding: 11px;
		padding-bottom: 40px;
		float: left;
		position: relative;
		margin-right: 30px;
		color: white;
		&.mr-n {
			margin-right: 0; }
		&>div {
			float: none;
			width: auto;
			&:nth-child(2) {
				padding: 0;
				background: none;
				border: 0 none; } }
		h4 {
			font-size: 14px;
			position: absolute;
			line-height: 40px;
			bottom: 0;
			color: #a7acc1;
			text-transform: uppercase; }
		.wrap {
			border-radius: 5px;
			overflow: hidden;
			div {
				width: 125px;
				float: left;
				height: 83px;
				line-height: 83px;
				text-align: center;
				color: white; }
			.deep-ft-color {
				color: #999999; } } } }

// grid container
.grid {
	.grid-info {
		&>div {
			float: none;
			width: auto; } }
	.grid-container {
		line-height: 110px; } }

// pagination
.nav1 {
	background: #42a8ff;
	min-width: 98px;
	padding: 0 24px;
	height: 40px;
	line-height: 37px;
	border-radius: 5px;
	text-align: center;
	float: left;
	margin-right: 123px;
	margin-bottom: 20px;
	button {
		width: 10px;
		height: 10px;
		border-radius: 50%;
		border: 2px solid white;
		margin: 0 6px;
		&.on {
			background: white; } } }
.nav2 {
	position: relative;
	float: left;
	button {
		color: #555765;
		background: white;
		width: 100%;
		height: 100%;
		&[class ^= go] {
			float: left;
			box-sizing: border-box;
			border: 1px solid #dadde5;
			width: 40px;
			height: 40px;
			position: absolute;
			font-size: 20px; }
		&.go-first {
			left: 0;
			border-radius: 5px 0 0 5px; }
		&.go-last {
			right: 0;
			border-radius: 0 5px 5px 0; } }
	ol {
		float: left;
		width: 400px;
		padding: 0 40px;
		height: 40px;
		overflow: hidden;
		li {
			float: left;
			display: block;
			box-sizing: border-box;
			border-right: 1px solid #dadde5;
			border-top: 1px solid #dadde5;
			border-bottom: 1px solid #dadde5;
			width: 40px;
			height: 40px;
			&::after {
				content: '';
				display: block;
				clear: both; }
			&:last-child {
				border-right: 0 none; }
			&.on {
				border: 0 none;
				button {
					background: #42a8ff;
					color: white; } } } } }
// * nav 줄이기 예제 *
@media screen and ( max-width : 1150px ) {
	.nav2 {
		ol {
			// width*length ( 40 * 3 )
			width: 120px; }
		button.go-last {
			right: 1px; } } }

