// input group
.input-group {
	&.type-1 {
		input {
			box-sizing: border-box;
			max-width: 600px;
			width: 100%;
			height: 40px;
			border-radius: 20px;
			padding: 0 30px;
			font-size: 14px;
			border-width: 1px;
			border-style: solid;
			&.normal {
				border: 1px solid #eceff9;
				background: #f4f7fc;
				color: map-get($gray, 4); }
			&.focus, &:focus {
				border-color: map-get($gray, 4);
				color: map-get($basic, 1);
				background: #f4f7fc; }
			&.complete {
				background: none;
				border-color: map-get($mint, 2);
				color: map-get($mint, 2); }
			&.disable {
				background: #e8ebf2;
				color: #999999; } } }
	.error-box {
		max-width: 600px;
		width: 100%;
		height: 40px;
		position: relative;
		input {
			color: map-get($gray, 4);
			border: 1px solid #de4040;
			background: #f4f7fc; }
		.error-message {
			font-size: 14px;
			color: #de4040;
			position: absolute;
			right: 22px;
			top: 50%;
			transform : translateY(-50%); } }
	&.type-2 {
		input, .select-box {
			box-sizing: border-box;
			max-width: 600px;
			width: 100%;
			height: 40px;
			border-radius: 5px;
			padding: 0 30px;
			font-size: 14px;
			border-width: 1px;
			border-style: solid;
			border-color: #c0c5d9;
			&.normal {
				color: map-get($gray, 4);
				border-color: map-get($gray, 4); }
			&.focus {
				color: map-get($basic, 1); }
			&.disable {
				color: #999;
				background: #f5f7fa; } }
		.select-box {
			position: relative;
			button {
				color: #333333;
				position: absolute;
				right: 0;
				width: 50px;
				height: 40px; }
			p {
				float: left;
				line-height: 40px;
				font-size: inherit; }
			&.focus {
				color: map-get($basic, 1);
				button {
					color: #42a8ff; } } } } }
.radio-group {
	div {
		box-sizing: border-box;
		width: 18px;
		height: 18px;
		border-radius: 50%;
		position: relative;
		span {
			position: absolute;
			display: block;
			width: 8px;
			height: 8px;
			border-radius: 50%;
			top: 3px;
			left: 3px; } }
	.type-1 {
		div {
			border: 2px solid #e1e3e6;
			&.on {
				span {
					background: #de40be; } } } }
	.type-2 {
		div {
			border: 2px solid #e1e3e6;
			background: #e8ebf2;
			&.on {
				span {
					background: #afb2c0; } } } } }
.checkbox-group {
	div {
		box-sizing: border-box;
		width: 20px;
		height: 20px;
		border-radius: 5px;
		position: relative;
		background: #eceff9;
		span {
			width: 20px;
			height: 20px;
			background: url('../images/check.svg') no-repeat;
			background-position: center center; }
		&.on {
			background: #de40be; } } }

