// Q&A
.question-wrapper {
	.q-a-wrap {
		width: 930px;
		line-height: 58px;
		border: 1px solid #eceff9;
		border-radius: 5px;
		font-size: 16px;
		color: #4e4e4e;
		&::after {
			content: '';
			display: block;
			clear: both; }
		.q-a {
			width: 49px;
			text-align: right;
			margin-right: 25px;
			font-size: 22px;
			font-family: 'open sans';
			font-weight: bold;
			float: left;
			line-height: 58px;
			&.q {
				color: #df40be; }
			&.a {
				color: #c0c5d9; } }
		.content {
			float: left;
			font-size: 16px;
			line-height: 24px;
			padding-top: 19px; }
		.open-bt {
			font-size: 22px;
			height: 60px;
			float: right;
			padding: 0 29px;
			color: #282828; }
		&.answer {
			margin-top: 20px;
			.content {
				margin-bottom: 82px; }
			.text-box {
				&::after {
					content: '';
					display: block;
					clear: both; } }
			ul {
				background: #f5f7fa;
				color: #494d66;
				li {
					margin: 0 44px;
					padding: 18px 0;
					padding-top: 19px;
					border-top: 1px solid #c0c5d9;
					font-size: 13px;
					line-height: 21px;
					&::after {
						content: '';
						display: block;
						clear: both; }
					&:first-child {
						border: 0 none; }
					.emphasis {
						font-size: 15px;
						font-weight: bold; }
					.text-box {
						float: left;
						line-height: 42px;
						&.more-line {
							line-height: 21px; } }
					.bt {
						margin-top: 1px;
						font-size: 12px;
						width: 165px;
						background: #df40be;
						height: 40px;
						color: #fff;
						float: right;
						border-radius: 25px;
						&.wrap {
							background: #c0c5d9;
							button {
								float: left;
								color: #fff;
								font-size: 12px;
								height: 40px;
								width: 50%;
								box-sizing: border-box;
								position: relative;
								&:first-child {
									&::after {
										content: '';
										display: block;
										width: 1px;
										height: 11px;
										background: #aeb4cb;
										position: absolute;
										right: 1px;
										top: 50%;
										transform: translateY(-50%); } } } } } } } } } }

// TAB
.tab-wrap {
	.menu {
		box-sizing: border-box;
		padding: 0 50px;
		position: relative;
		border: 1px solid #c0c5d9;
		border-radius: 5px;
		line-height: 38px;
		&>button {
			position: absolute;
			width: 50px;
			height: 40px;
			background: #c724a6;
			color: #fff;
			font-size: 20px;
			top: -1px;
			z-index: 10;
			&.right {
				border-radius: 0 5px 5px 0;
				right: -1px; }
			&.left {
				border-radius: 5px 0 0 5px;
				left: -1px; } }
		.see {
			width: 100%;
			overflow: hidden; }
		ul {
			width: 1327px;
			margin: 0 auto;
			@extend %clear;
			li {
				width: 6.6666666667%;
				white-space: nowrap;
				float: left;
				font-size: 16px;
				height: 38px;
				button {
					height: 38px;
					width: 100%; } } } }
	.tab {
		button {
			color: #3c3d41;
			width: 254px;
			height: 40px;
			border: 1px solid #c0c5d9;
			box-sizing: border-box;
			&:first-child {
				border-right: 0 none;
				border-radius: 5px 0 0 5px; }
			&:last-child {
				border-left: 0 none;
				border-radius: 0 5px 5px 0; }
			&.on {
				border-color: #c724a6;
				background: #c724a6;
				color: #fff; } } }
	.tab-menu {
		border-radius: 5px 5px 0 0;
		border: 1px solid #eaeaea;
		border-bottom: 3px solid #de40be;
		@extend %clear;
		li {
			float: left;
			margin-top: 13px;
			margin-bottom: 10px;
			&:first-child {
				margin-left: 29px; }
			&.on {
				button {
					background: #c724a6;
					color: #fff; } }
			button {
				padding: 0 22px;
				border-radius: 25px;
				line-height: 43px; } } }
	.tab-category {
		@extend %clear;
		font-size: 13px;
		display: table;
		margin-right: auto;
		margin-left: auto;
		li {
			float: left;
			margin-left: 10px;
			&:first-child {
				margin-left: 0; }
			&.on {
				button {
					background: #42a7ff;
					color: #fff; }
				span {
					font-weight: bold; } }
			button {
				box-sizing: border-box;
				color: #4e4e4e;
				border: 1px solid #eceff9;
				width: 178px;
				height: 40px;
				border-radius: 5px; } } } }
